.descr {
  @include cover;
  background-color: $white;
  padding: 120px 0 95px;
  @include xl {
    padding: 90px 0; }
  @include lg {
    padding: 70px 0; }
  @include md {
    padding: 50px 0; }
  @include sm {
    padding: 30px 0; }
  &__title {
    text-align: center;
    margin-bottom: 60px;
    max-width: 1430px;
    padding-left: 15px;
    padding-right: 15px;
    margin-left: auto;
    margin-right: auto;
    @include r(1460) {
      margin-left: -15px;
      margin-right: -15px; }
    @include lg {
      margin-bottom: 50px; }
    @include md {
      margin-bottom: 30px; }
    @include sm {
      margin-bottom: 15px; } }
  &__inner {
    max-width: 1430px;
    padding-left: 15px;
    padding-right: 15px;
    margin-left: auto;
    margin-right: auto;
    @include flex(flex-start, normal, row, wrap);
    @include r(1460) {
      margin-left: -15px;
      margin-right: -15px; } }
  &__block {
    width: 50%;
    padding-left: 15px;
    padding-right: 15px;
    @include xs {
      width: 100%; } } }

.descr-block {
  @include flex(flex-start, normal, row, wrap);
  &__img {
    @include cover;
    background-size: contain;
    padding-top: 40%;
    width: 40%;
    @include sm {
      width: 100%; } }
  &__content {
    @include flex(center, normal, column);
    width: 60%;
    @include sm {
      width: 100%; } }
  &__title {
    margin-bottom: 15px;
    @include sm {
      margin-bottom: 10px; } } }


