.instructions {
  &__title {
    text-align: center;
    margin-bottom: 50px;
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 15px; } } }

.instruction-block {
  $this: &;
  margin-bottom: 30px;
  margin-left: -22px;
  margin-right: -22px;
  @include flex(flex-start, normal, row, wrap);
  @include lg {
    margin-left: -15px;
    margin-right: -15px; }
  @include md {
    margin-left: -10px;
    margin-right: -10px; }
  @include sm {
    margin-bottom: 15px; }
  &:last-child {
    margin-bottom: 120px;
    @include xl {
      margin-bottom: 100px; }
    @include lg {
      margin-bottom: 80px; }
    @include md {
      margin-bottom: 50px; }
    @include sm {
      margin-bottom: 30px; } }

  &__left, &__right {
    width: 50%;
    padding-left: 22px;
    padding-right: 22px;
    @include lg {
      padding-left: 15px;
      padding-right: 15px; }
    @include md {
      paddin-left: 10px;
      paddin-right: 10px; }
    @include sm {
      width: 100%; } }
  &__right {
    @include sm {
      order: 0;
      margin-bottom: 15px; } }
  &__left {
    @include sm {
      order: 1; } }
  &__img {
    padding-top: 50%;
    @include cover; }
  &__content {
    height: 100%;
    @include flex(center, normal, column); }
  &__title {
    margin-bottom: 15px;
    @include md {
      margin-bottom: 10px; } }
  &__text {
    p, ul {
      @include notlast {
        margin-bottom: 15px; } }
    ul {
      li {
        &::before {
          content: '-';
          display: inline-block;
          margin-right: .5em; } } } }

  &--reverse {
    #{$this}__left {
      order: 1; }
    #{$this}__right {
      order: 0; } } }
