.sertificats {
  padding: 0;
  padding-bottom: 40px;
  @include lg {
    padding:  0 0 30px; }
  @include md {
    padding: 0 0 25px; }
  @include sm {
    padding: 0 0 15px; }
  &__top {
    border-bottom: 1px solid  $c-border;
    @include flex(flex-start, center);
    padding: 25px 90px;
    @include lg {
      padding: 15px 60px; }
    @include md {
      padding: 10px 40px; }
    @include sm {
      padding: 10px 20px; }
    @include xs {
      padding: 10px; } }
  &__title {
    font-size: 18px;
    color: $gray-dark;
    @include sm {
      font-size: $fz; } }
  &__logo {
    position: relative;
    padding-right: 35px;
    @include md {
      padding-right: 15px; }
    @include before {
      position: absolute;
      width: 1px;
      background-color: $c-border;
      height: 45px;
      right: 0;
      top: 50%;
      transform: translate(0, -50%); } }
  &__title {
    padding-left: 35px;
    @include md {
      padding-left: 15px; } }
  &__inner {
    @include flex(center, normal, row, wrap);
    margin-left: -15px;
    margin-right: -15px;
    padding-left: 90px;
    padding-right: 90px;
    padding-top: 50px;
    padding-bottom: 10px;
    @include lg {
      padding: 40px 60px 10px; }
    @include md {
      padding: 20px 40px 0px;
      margin-right: -5px;
      margin-left: -5px; }
    @include sm {
      padding: 10px 20px; }
    @include xs {
      padding: 10px; } }
  &__btn {
    text-align: center; } }

.sertificat {
  background-color: $gray-lightest;
  border-radius: $radius-sm;
  position: relative;
  cursor: pointer;
  // height: 145px
  @include flex(center, center);
  padding: 44px 0;
  height: 100%;
  // border: 1px solid transparent
  // +tr(border-color .3s, box-shadow .3s)
  @include hover {
    .radio {
      span::before {
        border-color: $accent;
        box-shadow: 0px 2px 9px 0px rgba(0, 0, 0, 0.19); } } }
  .radio {
    input[type="radio"] {
      &:checked + span {
        @include before {
          border-color: $accent;
          background-color: transparent;
          box-shadow: 0px 2px 9px 0px rgba(0, 0, 0, 0.19); } } }
    span {
      @include before {
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        border-radius: $radius-sm;
        border-color: transparent;
        pointer-events: none;
        @include tr(border-color .3s, box-shadow .3s); } } }
  &.is-disabled {
    opacity: .4;
    pointer-events: none; }
  &__wrap {
    width: 25%;
    padding-left: 15px;
    padding-right: 15px;
    margin-bottom: 30px;
    @include lg {
      width: 33.33333%; }
    @include md {
      padding-left: 5px;
      padding-right: 5px;
      margin-bottom: 10px; }
    @include sm {
      width: 50%; }
    @include xs {
      width: 100%; } }
  &__label {
    background-color: $accent;
    display: block;
    font-size: 9px;
    @include bold;
    color: $white;
    padding: 7px 10px 6px;
    border-top-right-radius: $radius-sm;
    border-top-left-radius: $radius-sm;
    border-bottom-right-radius: $radius-sm;
    text-align: center;
    text-transform: uppercase;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    max-width: 72%; }
  &__logo {
    position: absolute;
    display: block;
    z-index: 1;
    top: 10px;
    right: 10px; }
  &__title {
    display: block; } }

.sertificats-nav {
  @include xs {
    order: 1;
    font-size: 0;
    max-height: 0;
    overflow: hidden;
    &.is-active {
      max-height: none;
      overflow: visible; } }
  &__btn {
    display: none;

    // +bold
    // text-transform: uppercase
    // text-align: center
    // background-color: transparent
    // color: $accent
    position: relative;
    order: 0;
    @include after {
      @include icon-arrow(8, 8, 2, currentColor, -45);
      display: inline-block;
      margin-left: 10px;
      position: relative;
      top: -.15em; }
    @include xs {
      display: block; }
    &.is-active {
      margin-bottom: 15px;
      @include after {
        @include rotate(135);
        top: .15em; } } }
  &__link {
    @include xs {
      width: 33.33333%;
      vertical-align: top;
      margin-bottom: 20px;
      img {
        max-width: 70%; } } }
  &__wrap {
    @include xs {
      @include flex(flex-start, center, column); } } }

