.user-img-change {
  @include flex(center, center);
  color: $gray;
  .user-img {
    margin-right: 10px; }
  a {
    text-decoration: underline; } }

.user-img {
  width: 108px;
  height: 108px;
  flex: 0 0 108px;
  border-radius: 50%;
  background-color: $white;
  @include cover;
  background-size: auto;
  @include md {
    width: 90px;
    height: 90px;
    flex: 0 0 90px; }
  @include sm {
    width: 60px;
    height: 60px;
    flex: 0 0 60px;
    display: inline-block;
    vertical-align: middle; }

  &--gray {
    background-color: $gray-lightest; } }

.cabinet {
  margin-top: -$header-height;
  padding-top: $header-height;
  margin-bottom: -$footer-height;
  padding-bottom: $footer-height;
  position: relative;
  background-color: #eaeaf6;
  min-height: 100vh;
  @include before {
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    right: 0;
    height: $header-full-height;
    border-bottom-left-radius: $radius;
    border-bottom-right-radius: $radius;
    background-color: $blue;
    @include lg {
      height: $header-full-height-lg; } }
  @include lg {
    margin-top: -$header-height-lg;
    padding-top: $header-height-lg; }
  @include sm {
    margin-top: -$header-height-sm;
    padding-top: $header-height-sm; }
  &__souvenirs {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px; }
  &__inner {
    position: relative;
    z-index: 1;
    margin-left: 198px;
    max-width: 1360px;
    padding-left: 15px;
    padding-right: 15px;
    @include xll {
      margin-left: auto;
      margin-right: auto; } }
  &__top {
    color: $white;
    @include flex(space-between, center, row);
    margin-bottom: 20px;
    @include sm {
      display: block; } }
  &__img {}
  &__title {
    flex-grow: 1;
    text-transform: uppercase;
    padding-left: 30px;
    padding-right: 30px;
    @include bold;
    @include css-lock(16, 24, 320, 1199, font-size);
    @include xl {
      padding-left: 15px;
      padding-right: 15px; }
    @include sm {
      padding-right: 0;
      display: inline-block;
      vertical-align: middle;
      width: calc(100% - 65px); } }
  &__btn {
    @include sm {
      margin-top: 15px; } }
  &__blocks {
    @include flex(flex-start, normal, row, wrap);
    margin-left: -15px;
    margin-right: -15px;
    @include notlast {
      margin-bottom: 90px;
      @include xl {
        margin-bottom: 70px; }
      @include lg {
        margin-bottom: 50px; }
      @include md {
        margin-bottom: 30px; }
      @include sm {
        margin-bottom: 15px; } } }
  &__blocks-slider {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
    margin-bottom: 30px; }
  &__block {
    width: 100%;
    margin-bottom: 30px;
    padding-left: 15px;
    padding-right: 15px; }
  &__block-2-3 {
    width: percentage(2/3);
    @include md {
      width: 100%; } }
  &__block-1-3 {
    width: percentage(1/3);
    @include md {
      width: 100%; } }
  &__block-center {
    max-width: 590px;
    margin-left: auto;
    margin-right: auto; }
  &__block-6-8 {
    width: percentage(6/8);
    max-width: none; }
  &__block-form {
    max-width: 886px; } }

.cabinet {
  $this: &;
  &--page {
    background-color: $white;
    padding-top: $header-height + 50px;
    @include before {
      @include md {
        height: $header-full-height-md; }
      @include sm {
        height: $header-full-height-sm; } }
    @include lg {
      padding-top: $header-height-lg + 50px; }
    @include md {
      padding-top: $header-height-lg; }
    @include sm {
      padding-top: $header-height-sm; }
    #{$this}__title {
      // width: 100%
      @include sm {
        padding-left: 0;
        width: 100%; } }
    #{$this}__inner {
      max-width: none;
      margin-left: 0;
      padding-left: 65px;
      padding-right: 55px;
      @include lg {
        padding-left: 40px;
        padding-right: 40px; }
      @include sm {
        padding-left: 15px;
        padding-right: 15px; } }
    #{$this}__page {
      padding-top: $header-full-height - 150px;
      @include lg {
        padding-top: $header-full-height-lg - 150px; }
      @include md {
        padding-top: $header-full-height-md - 100px; }
      @include sm {
        padding-top: $header-full-height-sm - 70px; } } } }

.cabinet-block {
  background-color: $white;
  border-radius: $radius;
  box-shadow: 0px 5px 30px 0px rgba(0, 0, 0, 0.08);
  padding: 30px 40px;
  position: relative;
  @include lg {
    padding: 30px; }
  @include md {
    padding: 25px; }
  @include sm {
    padding: 15px;
    border-radius: $radius-sm; }
  &__title {
    color: $gray;
    @include bold;
    font-size: 18px;
    @include sm {
      font-size: 16px; } }
  &__right-bottom {
    position: absolute;
    bottom: 20px;
    right: 20px; }

  &--hor {
    @include flex(center, center);
    min-height: 160px;
    .number {
      margin-left: 10px; }
    @include lg {
      min-height: 120px; }
    @include xs {
      min-height: 100px; } } }


.cabinet-table {
  color: $gray-dark;
  table {
    th {
      font-weight: 400;
      @include bold;
      font-size: 18px;
      color: $gray;
      text-align: left;
      &:nth-shild(1) {
        width: 70%; }
      &:nth-shild(2) {
        width: 30%; }
      @include sm {
        font-size: 16px; } }
    tr {
      @include notlast {
        border-bottom: 1px solid  $gray-light; }
      &:last-child {
        td {
          padding-bottom: 0; } } }
    td, th {
      padding: 20px 10px;
      @include lg {
        padding: 15px 10px; }
      @include md {
        padding: 10px; }
      @include sm {
        padding: 5px 10px; } } } }
